export function multiply(z1, z2){
	return {
		x: (z1.x * z2.x) - (z1.y * z2.y),
		y: (z1.x * z2.y) + (z1.y * z2.x)
	}
}
  
export function add(z1, z2){
	return {
		x: z1.x + z2.x,
		y: z1.y + z2.y
	}
}

export function subtract(z1, z2){
	return {
		x: z1.x - z2.x,
		y: z1.y - z2.y
	}
}

export function magnitudeSquared(z){
	return (z.x * z.x) + (z.y * z.y)
}

export function magnitude(z){
	return Math.pow(magnitudeSquared(z), 0.5)
}

export function cartesianToPolar(z){
	let r = magnitude(z);
	return {
		r,
		theta: r === 0 ? 0 : Math.atan2(z.y, z.x)
	}
}

export function polarToCartesian(zP){
	return {
		x: zP.r * Math.cos(zP.theta),
		y: zP.r * Math.sin(zP.theta)
	}
}

export function powerPolar(zP, k){
	return {
		r: Math.pow(zP.r, k),
		theta: k * zP.theta
	}
}

// 3 * -pi/4 = -3pi/4
// 3 * 7pi/4 = 21pi/4 = 8pi/4 + 8pi/4 + 5pi/4

export function power(z, k){
	let zP = cartesianToPolar(z);
	let zPResult = powerPolar(zP, k);
	//console.log(JSON.stringify({z, zP, zPResult}, null, 2));

	return polarToCartesian(zPResult);
}


export class Iterator {
	constructor(threshold, iterate){
		this.threshold = threshold;
		//iterate is a function of the form iterate(z, c)
		this.iterate = iterate;
	}
	//Mandelbrot: (0, c) => 0^2 + c => c^2 + c => ...
	//Julia(c): (z0, c)
	calculateEscape(z0, c, nIterations){
		let i = 0;
		let z = z0;
		while(i < nIterations - 1){
			if(magnitude(z) > this.threshold) break;
			z = this.iterate(z, c);
			i++;
		}
		return i;
	}

}

export function iteratorTest(){
	let iterateA = function(z, c){
		console.log('accurate');
		return add(multiply(multiply(z,z),z),c);
	}
	let iterateB = function(z, c){
		return add(power(z,3),c);
	}

    let nIterations = 100;
	let threshold = 10;
	
	let iteratorA = new Iterator(threshold, iterateA);
	let iteratorB = new Iterator(threshold, iterateB);

	let delta = 1;
	let tolerance = 0;
	for(let x = -3; x < 3; x += delta){
		for(let y = 0; y < 3; y += delta){
			let c = {x, y};
			let z0 = {x:0, y:0}

			let difference = iteratorA.calculateEscape(z0, c, nIterations) - iteratorB.calculateEscape(z0, c);
			if (difference !== 0) console.log(JSON.stringify({difference, c}, null, 2));
		}
	}

}