export default class ColorHandler{
  constructor(colors, params){
		this.colors = colors;
		this.params = params;
		this.colorMap = this.createColorMap();
	}

	setColors(colors){
		this.colors = colors;
		this.colorMap = this.createColorMap();
	}

	setParams(params){
		this.params = params;
		this.colorMap  = this.createColorMap();
	}
	
	RGBToHex(r, g, b) {
		//Got this from CSS-tricks
		r = r.toString(16);
		g = g.toString(16);
		b = b.toString(16);

		if (r.length == 1)
			r = "0" + r;
		if (g.length == 1)
			g = "0" + g;
		if (b.length == 1)
			b = "0" + b;
		return "#" + r + g + b;
	}

	getRGBMix(rgbA, rgbB, ratio){
		let rgbMix = [];
		let keys = ['r', 'g', 'b'];
		keys.forEach(key => {
			rgbMix.push(Math.floor(rgbA[key] + (1 -ratio) * (rgbB[key] - rgbA[key])));
		});
		return rgbMix;
	}

	createColorMap(){
		let map = {};
		let count = 0;
		let colorIndex = 0;
		let nextColorIndex = 1;
		this.maxIterations = this.params.nColorRepeatsMax * this.params.colorWidthMax * (this.colors.length - 1);

		for(let i = 0; i < this.maxIterations; i++){
			if(count === this.params.colorWidth){
				colorIndex = nextColorIndex;
				nextColorIndex++;
				//The - 1 is because the set color is given bt colors[colors.length - 1]
				if(nextColorIndex === this.colors.length - 1) nextColorIndex = 0;
				count = 0;
			}
			let ratio = (this.params.colorWidth - count) / this.params.colorWidth;
			map[i] = this.RGBToHex(...this.getRGBMix(this.colors[colorIndex], this.colors[nextColorIndex], ratio ));
			count++;
		}
		//reserve an extra top 'colorWidth'
		//for the transition from color[colors.length - 2] to color[colors.length - 1]
		//colors[colors.length - 1] only appears as the set color
		for(let i = 0; i < this.params.colorWidth; i++){
			let ratio = (this.params.colorWidth - i) / this.params.colorWidth;
			map[i + this.maxIterations] = this.RGBToHex(...this.getRGBMix(this.colors[this.colors.length - 2], this.colors[this.colors.length - 1], ratio ));
		}
		return map;
	}

	getColor(escape, nIterations){
		let iterationsIntoFinalColor = escape - (nIterations - this.params.colorWidth);
		return iterationsIntoFinalColor > 0 ? this.colorMap[this.maxIterations + iterationsIntoFinalColor ] : this.colorMap[escape];
	}
}