<template>
  <div class="main-holder">
    <div class="canvas-panel" ref="ch">
      <canvas id="graphic-canvas" data-paper-hidpi="off"></canvas>
    </div>
    <div class="control-panel">
      <div class="mode-buttons-holder">
        <div class="mode-button" v-bind:class="{'mode-button-selected':touchMode!=='zoom'}" v-on:click="setMode('swap')">
          <i class="material-icons not-selectable">swap_horiz</i>
        </div>
        <div class="mode-button" v-bind:class="{'mode-button-selected':touchMode==='zoom'&&zoomSign==='zoom-plus'}" v-on:click="setMode('zoom-plus')">
          <i class="material-icons not-selectable">add</i>
        </div>
        <div class="mode-button" v-bind:class="{'mode-button-selected':touchMode==='zoom'&&zoomSign==='zoom-minus'}" v-on:click="setMode('zoom-minus')">
          <i class="material-icons not-selectable">remove</i>
        </div>
      </div>
      <div class="title-holder">
        <div class="hTitle">FRACTAL</div>
        <div class="hSubtitle">explorer</div>
      </div>
      <div class="control-buttons-holder">
        <div class="control-button" v-on:click="showPopup()">
          <i class="material-icons not-selectable">settings</i>
        </div>
        <div class="control-button" v-on:click="reset()">
          <i class="material-icons not-selectable">refresh</i>
        </div>
        <div class="control-button" v-on:click="showInfo()">
          <i class="material-icons not-selectable">help</i>
        </div>
      </div>
    </div>
    <div class="overlay" v-bind:class="{hidden:overlayIsHidden}">
      <div class="overlay-text">CALCULATING</div>
    </div>
    <div class="popup-holder" v-bind:class="{hidden:infoIsHidden}">
      <h1>About</h1>
      <div class="popup-scroller">
        Blah blah blah.
        v1.1
      </div>
      <div class="popup-buttons-holder">
        <span class="popup-button" v-on:click="hideInfo()">OK</span>
      </div>
    </div>
    <div class="popup-holder" v-bind:class="{hidden:popupIsHidden}">
      <h1>Settings</h1>
      <div class="popup-scroller">
        <h3>Zoom</h3>
        <div class="popup-group">
          <Slider v-bind:uniqueRef="'colorWidthSlider'" v-bind:param="getZoomParam()"></Slider>
        </div>
        <div class="popup-separator"/>
        <h3>Colors</h3>
        <div class="popup-group">
          <Slider v-bind:uniqueRef="'colorWidthSlider'" v-bind:param="getColorWidthParam()"></Slider>
          <Slider v-bind:uniqueRef="'colorRepeatsSlider'" v-bind:param="getColorRepeatsParam()"></Slider>
        </div>
        <div class="popup-group">
          <Color v-bind:colors="colors"></Color>
        </div>
      </div>
      <div class="popup-buttons-holder">
        <span class="popup-button" v-on:click="update()">OK</span>
      </div>
    </div>
  </div>
</template>

<script>
import * as complex from '../utilities/complex.js';
import { bus } from '../main';
import Color from './Color';
import Slider from './Slider';
import ColorHandler from '../utilities/colorHandler.js';

export default {
  name: 'Graphic',
  props: {},
  components:{
    Color, Slider
  },
  data:function(){
    return {
      colors:[
        //The cutoff is the escape iterations number at which the color is at its purest.
        {r:100, g:0, b:0}, // Most distant color
        {r:255, g:150, b:150},
        {r:255, g:255, b:255},
        {r:0, g:0, b:0} // Set color
      ],
      isReady:false,
      popupIsHidden:true,
      infoIsHidden:true,
      overlayIsHidden:true,
      touchMode:'zoom',
      zoomSign:'zoom-plus'
    }
  },
  methods:{
    reset:function(){
      bus.$emit('reset');
    },
    setMode:function(mode){
      console.log(mode);
      if(mode === 'swap'){
        this.touchMode = 'swap';
      } else {
        this.touchMode = 'zoom';
        this.zoomSign = mode;
        console.log(this.zoomSign);
      }
    },
    showPopup:function(){
      this.popupIsHidden = false;
      this.overlayIsHidden = false;
      this.updateState = {
        zoomChanged: false,
        colorsChanged: false,
        paramsChanged: false
      }
    },
    showInfo:function(){
      this.infoIsHidden = false;
      this.overlayIsHidden = false;
    },
    hideInfo:function(){
      this.infoIsHidden = true;
      this.overlayIsHidden = true;
    },
    update:function(){
      if(this.updateState.colorsChanged || this.updateState.paramsChanged){
        bus.$emit('update');
      } else {
        this.popupIsHidden = true;
        this.overlayIsHidden = true;
      }
    },
    getZoomParam:function(){
      return { 
        isDiscrete:true, 
        name: 'Zoom Multiplier',
        key: 'zoomMultiplier', 
        min:1, 
        max:50, 
        default:10
      }
    },
    getColorWidthParam:function(){
      return { 
        isDiscrete:true, 
        name: 'Color Width',
        key: 'colorWidth', 
        min:5, 
        max:100, 
        default:50
      }
    },
    getColorRepeatsParam:function(){
      return { 
        isDiscrete:true, 
        name: 'Color Repeats',
        key: 'nColorRepeats', 
        min:1, 
        max:10, 
        default:1
      }
    }
  },
  mounted(){
    let canvasHolder = this.$refs.ch;
    console.log([canvasHolder.offsetHeight, canvasHolder.offsetWidth]);
    console.log([this.touchMode, this.zoomSign]);
    //let sideLength = Math.floor(Math.min(canvasHolder.offsetWidth, canvasHolder.offsetHeight));
    let canvas = document.getElementById('graphic-canvas');
    canvas.width = Math.floor(canvasHolder.offsetWidth);//sideLength;
    canvas.height = Math.floor(canvasHolder.offsetHeight);//sideLength;
    let ctx = canvas.getContext('2d');
    //paper.setup(canvas);

    let self = this;

    let xRangeMin = 0;
    let xRangeMax = canvas.width;

    let yRangeMin = canvas.height;
    let yRangeMax = 0;

    var xMin, xMax, yMin, yMax;
    var xScaleFactor, yScaleFactor;

    let getXScaleFactor = function(){
      return (xRangeMax - xRangeMin) / (xMax - xMin);
    }

    let getYScaleFactor = function(){
      return (yRangeMax - yRangeMin) / (yMax - yMin);
    }

    let summarizeDimensions = function(){
      return {
        xMin, xMax, xRangeMin, xRangeMax, xScaleFactor, 
        yMin, yMax, yRangeMin, yRangeMax, yScaleFactor
      }
    }

    let setBounds = function(_xMin, _xMax, _yMin, _yMax){
      console.log('setBounds');
      xMin = _xMin;
      xMax = _xMax;

      yMin = _yMin;
      yMax = _yMax;

      xScaleFactor = getXScaleFactor();
      yScaleFactor = getYScaleFactor();

      console.log(summarizeDimensions());
    }

    let getBounds = function(){
      return { xMin, xMax, yMin, yMax }
    }

    let resetBounds = function(){
      let ratio = canvas.height / canvas.width;
      setBounds(-2.5, 2.5, ratio * -2.5, ratio * 2.5);
    }

    let getXDomain = function(x){
      return ((x - xMin) * xScaleFactor) + xRangeMin;
    }
    let getYDomain = function(y){
      return ((y - yMin) * yScaleFactor) + yRangeMin;
    }

    let getX = function(xRange){
      return ((xRange - xRangeMin) / xScaleFactor) + xMin;
    }

    let getY = function(yRange){
      return ((yRange - yRangeMin) / yScaleFactor) + yMin;
    }

    let colors;
    let resetColors = function(){
      colors = self.colors;
    }

    let zoomMultiplier, iterationMode;
    let resetMode = function(){
      zoomMultiplier = 10;
      iterationMode = 'main'; // vs. 'julia'
    }

    resetBounds();
    resetColors();
    resetMode();


    let getZoomFactor = function(){
      if(self.zoomSign === 'zoom-plus'){
        return params.zoomMultiplier;
      }
      return 1 / params.zoomMultiplier;
    }

    let reframeToRangePoint = function(xRange, yRange, zoomFactor){
      console.log({zoomFactor})
      console.log(summarizeDimensions());
      let insetRangeWidth = (xRangeMax - xRangeMin) / zoomFactor;
      let insetRangeHeight = (yRangeMax - yRangeMin) / zoomFactor;

      console.log({insetRangeWidth, insetRangeHeight});

      let xMinNew = getX(xRange - insetRangeWidth/2);
      let xMaxNew = getX(xRange + insetRangeWidth/2);
      let yMinNew = getY(yRange - insetRangeHeight/2);
      let yMaxNew = getY(yRange + insetRangeHeight/2);

      setBounds(xMinNew, xMaxNew, yMinNew, yMaxNew);
    }

    let reframeToDomainPoint = function(x, y, zoomFactor){
      console.log('reframeToDomainPoint')
      let xDomainWidth = (xMax - xMin) / zoomFactor;
      let yDomainWidth = (yMax - yMin) / zoomFactor;
      console.log({xDomainWidth, yDomainWidth})
      setBounds(x - xDomainWidth/2, x + xDomainWidth/2, y - yDomainWidth / 2, y + yDomainWidth / 2);
    }
    
    let threshold = 2;
    let iterate = function(z, c){
      return complex.add(complex.power(z,2),c);
    }
    let iterator = new complex.Iterator(threshold, iterate);


    
    let params = {
      nColorRepeats: this.getColorRepeatsParam().default,
      colorWidth: this.getColorWidthParam().default,
      nColorRepeatsMax: this.getColorRepeatsParam().max,
      colorWidthMax: this.getColorWidthParam().max,
      zoomMultiplier: this.getZoomParam().default
    }

    let getNIterations = function(){
      return params.nColorRepeats * params.colorWidth * (self.colors.length - 1);
    };

    let nIterations = getNIterations();
    let colorHandler = new ColorHandler(colors, params);
    //let layer = project.activeLayer;
    
    //Set up grid;
    let grid = []
    for(let xRange = xRangeMin; xRange < xRangeMax; xRange++){
      let column = [];
      for(let yRange = yRangeMax; yRange < yRangeMin; yRange++){
        //let rect = new Rectangle(new Point(xRange, yRange), new Size(1,1));
        //let shape = new Path.Rectangle(rect);
        //shape.fillColor='black';
        //layer.addChild(shape);
        ctx.fillStyle = 'black';
        ctx.fillRect(xRange, yRange, 1, 1);

        let cell = {
          xRange,
          yRange
        }
        column.push(cell);
      }
      grid.push(column);
    }
    let updateGrid = function(callback){
      self.popupIsHidden = true;
      self.overlayIsHidden = false;
      setTimeout(function(){
        grid.forEach((column, xRange) => {
          column.forEach((cell, yRange) => {
            callback(cell, xRange, yRange);
            if(xRange === grid.length - 1 && yRange === column.length - 1){
              self.overlayIsHidden = true;
            }
          });
        });
      }, 1);
    }

    let iterateAndColor = function(cell){
      let escape = iterator.calculateEscape(cell.iterationParams.z0, cell.iterationParams.c, nIterations);
      ctx.fillStyle = colorHandler.getColor(escape, nIterations);
      ctx.fillRect(cell.xRange, cell.yRange, 1, 1);
      cell.escape = escape;
    }

    //I got this from: https://www.informit.com/articles/article.aspx?p=1903884&seqNum=6
    function windowToCanvas(cnv, x, y) {
      var bbox = cnv.getBoundingClientRect();
      return { 
        xRange: x - bbox.left * (cnv.width  / bbox.width),
        yRange: y - bbox.top  * (cnv.height / bbox.height)
      };
    }

    // swap:main redraw: draw main zoomed one (?) factor to point cJulia;
    // swap:julia redraw(cJulia): draw julia corresponding to cJulia; resetBounds;
    let redraw = function(cJulia){
      console.log([cJulia, self.touchMode, self.zoomSign]);
      updateGrid((cell, xRange, yRange) => {
        let complexCoords = {x:getX(xRange), y:getY(yRange)};
        var z0, c;
        if(cJulia){
          z0 = complexCoords;
          c = cJulia;
        } else {
          z0 = {x:0, y:0};
          c = complexCoords;
        }
        cell.iterationParams = { z0, c }
        iterateAndColor(cell);
      });
      //REDEFINE MOUSE INTERACTION FOR THE CANVAS AS A WHOLE
      canvas.onmousedown = function(e){
        let screenCoords = windowToCanvas(canvas, e.clientX, e.clientY);
        let complexCoords = {x:getX(screenCoords.xRange), y:getY(screenCoords.yRange)};
        if(self.touchMode === 'zoom'){
          reframeToRangePoint(screenCoords.xRange, screenCoords.yRange, getZoomFactor());
        } else {
          console.log('Non-zoom interaction. Bounds will be reset to initial value.');
          resetBounds();
          //Swap view from current iteration mode
          if(iterationMode === 'main'){
            console.log('Switching to Julia set view.')
            //Swap to Julia, unzoomed view;
            iterationMode = 'julia';
          } else {
            //Swap back to main, zoom in somewhat
            console.log(`Swapping back to main view, zoom to: ${cJulia.x}, ${cJulia.y}` )
            iterationMode = 'main';
            reframeToDomainPoint(cJulia.x, cJulia.y, 100);
          }
        }
        if(iterationMode === 'main'){
          redraw();
        } else {
          if(cJulia){
            redraw(cJulia);
          } else {
            redraw(complexCoords);
          }
        }
      }
    }

    let recolor = function(){
      updateGrid(function(cell, xRange, yRange){
        ctx.fillStyle = colorHandler.getColor(cell.escape, nIterations);
        ctx.fillRect(cell.xRange, cell.yRange, 1, 1);
      })
    }

    let reiterate = function(){
      updateGrid(function(cell, xRange, yRange){
        iterateAndColor(cell);
      });
    }

    let reset = function(){
      resetBounds();
      resetColors();
      resetMode();
      redraw();
    }

    bus.$on('reset', () => {
      reset();
    })


    redraw();
    this.isReady = true;





    bus.$on('update-colors', cols => {
      this.updateState.colorsChanged = true;
      colors = cols;
      console.log(this.updateState);
    });

    bus.$on('update-param', valueObj => {
      //Only log a change in params that require a redraw.
      console.log(valueObj.name);
      if(valueObj.name !== 'zoomMultiplier'){
        this.updateState.paramsChanged = true;
      }
      params[valueObj.name] = valueObj.value;
      console.log(this.updateState);
    });

    bus.$on('update', () => {
      if(this.updateState.colorsChanged){
        colorHandler.setColors(colors);
        //recolor();
      }
      if(this.updateState.paramsChanged){
        colorHandler.setParams(params);
        nIterations = getNIterations();
        reiterate();
      } else if(this.updateState.colorsChanged){
        recolor();
      }
    })
  }
}
</script>

