<template>
	<div id="color-picker" ref="cph"></div>
</template>

<script>
import iro from '@jaames/iro';
import { bus } from '../main';

export default {
  name: 'Color',
	date: function(){
		return {
			colorPicker: null
		}
	},
	props:[
		'colors'
	],
	methods:{
		select:function(){
			let colors = [];
			this.colorPicker.colors.forEach(c => {
				colors.push(c.rgb);
			});
			console.log(colors);
			bus.$emit('update-colors', colors )
		}
	},
	mounted(){
		let colorPickerHolder = this.$refs.cph;
		this.colorPicker = new iro.ColorPicker('#color-picker', {
			colors:this.colors,
			width:colorPickerHolder.offsetWidth
		});
		let self = this;
		this.colorPicker.on('color:change', function(color){
			console.log(color);
			let colors = [];
			self.colorPicker.colors.forEach(c => {
				colors.push(c.rgb);
			});
			console.log(colors);
			bus.$emit('update-colors', colors )
		})
	}
}
</script>

