<template>
  <div class="slider-outer">
		<div class="slider-text">{{param.name}}:{{tidyValue(current)}}</div>
		<div class="slider-holder">
			<svg class="slider-svg" width="100%" height="100%" v-bind:ref="uniqueRef"></svg>
		</div>
	</div>
</template>
<script>
import { bus } from '../main';
import Snap from 'imports-loader?this=>window,fix=>module.exports=0!snapsvg/dist/snap.svg.js'
import interact from 'interactjs/dist/interact.js'

export default {
  name: "Slider",
  props:[
    'uniqueRef', 'param'
	],
	data:function(){
		return {
			current: this.param.default,
			delta: 0.1 * (this.param.max - this.param.min),
			rangeMin: null,
			rangeMax: null
		}
	},
  methods:{
		tidyValue:function(value){
			return Math.floor(value)
		},
		valueToRange:function(value){
			return this.rangeMin + this.scaleFactor * (value - this.param.min);
		},
		rangeToValue:function(rangeValue){
			return this.param.min + ((rangeValue - this.rangeMin)/this.scaleFactor);
		},
		emitUpdate(){
			let valueObject = {
				name:this.param.key, value:this.param.isDiscrete ? Math.floor(this.current): this.current
			};
			bus.$emit('update-param', valueObject);
		}
  },
  mounted(){
		console.log(interact);
		let svg = this.$refs[this.uniqueRef];
		let width = svg.clientWidth;
		let height = svg.clientHeight;
		let draw = Snap(svg);

		let ySlitMiddle = 0.5 * height;
		let slitHeight = 0.8 * height;
		let xSlitLeft = 0.01 * width;
		let xSlitRight = 0.99 * width;
		let slitWidth = xSlitRight - xSlitLeft;

		let handleWidth = slitWidth / 5;
		let handleHeight = slitHeight;
		let yHandleTop = ySlitMiddle - handleHeight / 2;
		let handleRadius = 0.2 * handleHeight;

		let xHandleLeftMin = xSlitLeft;
		let xHandleLeftMax = xSlitRight -handleWidth;

		this.rangeMin = xHandleLeftMin;
		this.rangeMax = xHandleLeftMax;
		this.scaleFactor = (this.rangeMax - this.rangeMin) / (this.param.max - this.param.min);
		
		let xHandleLeft = this.valueToRange(this.current);

		let slit = draw.line(xSlitLeft, ySlitMiddle, xSlitRight, ySlitMiddle);
		slit.attr({
			stroke:'black',
			strokeWidth:slitHeight / 2,
			opacity:0.2
		});
		let handle = draw.rect(xHandleLeft, yHandleTop, handleWidth, handleHeight, handleRadius, handleRadius);
		handle.attr({
			stroke:'grey',
			strokeWidth:1
		});
		handle.addClass('handle');


		let xHandleLeftStart = null;
		let self = this;

		let isTouching = false;
		let xTouchStart = null;

		interact(handle.node).draggable({
			listeners: {
				start (event) {
					xHandleLeftStart = +handle.attr('x');
				},
				move (event) {
					let xHandleLeftDragged = xHandleLeftStart + (+event.dx);
					if(xHandleLeftDragged < xHandleLeftMin) xHandleLeftDragged = xHandleLeftMin;
					if(xHandleLeftDragged > xHandleLeftMax) xHandleLeftDragged = xHandleLeftMax;
					handle.attr('x', xHandleLeftDragged);
					self.current = self.rangeToValue(xHandleLeftDragged);
					xHandleLeftStart = xHandleLeftDragged;
				},
				end (event) {
					console.log(event.type);
					self.emitUpdate();
				}
			}
		});
	}
}
</script>
<style>
.slider-outer {
	margin:2px;
	padding:4px;
	touch-action:none;
}
.slider-text{
	font-size:0.7em;
	text-align:left;
}
.slider-holder {
	height:1em;
}
.handle {
	fill:var(--color-6);
	stroke:var(--color-7);
}
</style>